*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  font-family: inherit;
  font-weight: inherit;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

input {
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

svg {
  width: 24px;
  height: 24px;
  display: block;
}

address {
  font-style: normal;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

input[type="search"]::-webkit-search-decoration, input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-results-button, input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal Regular), local(Neue-Montreal-Regular), url("NeueMontreal-Regular.7559b1a8.woff2") format("woff2"), url("NeueMontreal-Regular.f021a830.woff") format("woff"), url("NeueMontreal-Regular.e32a9fa8.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal Medium), local(Neue-Montreal-Medium), url("NeueMontreal-Medium.0edc0724.woff2") format("woff2"), url("NeueMontreal-Medium.3c93ed65.woff") format("woff"), url("NeueMontreal-Medium.14ce3e89.ttf") format("truetype");
  font-weight: 500;
}

@font-face {
  font-family: Quincy CF;
  src: local(Quincy CF Black), local(Quincy-CF-Black), url("QuincyCF-Black.4cf8f54f.woff2") format("woff2"), url("QuincyCF-Black.11a26a8c.woff") format("woff"), url("QuincyCF-Black.4fe7690f.ttf") format("truetype");
}

:root {
  color: #010101;
  background-color: #e8e8e2;
  font-size: 16px;
  font-weight: 500;
}

body {
  font-family: "system-ui", -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Neue Montreal;
}

body.lock {
  overflow: hidden;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo {
  letter-spacing: -.02em;
  align-items: center;
  gap: 8px;
  font-family: Quincy CF;
  font-size: 18px;
  line-height: 1.11;
  display: flex;
}

.logo__space {
  width: 28px;
  height: 28px;
  background-color: #6d8434;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.logo__icon {
  width: 16px;
  height: 16px;
  fill: #e8e8e2;
}

.title {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.products__filter-select.ss-main {
  width: 100%;
  height: 48px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(1, 1, 1, .3);
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  padding: 0 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  transition: border-color .3s linear;
}

.products__filter-select.ss-main.ss-open-below {
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.products__filter-select.ss-main .ss-values .ss-placeholder {
  color: #010101;
}

.products__filter-select.ss-main .ss-content {
  max-height: max-content;
  margin-top: 8px;
}

.products__filter-select.ss-main:focus {
  box-shadow: initial;
}

body .ss-content {
  max-height: max-content;
}

body .ss-content .ss-list {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

body .ss-content .ss-list .ss-option {
  color: rgba(1, 1, 1, .5);
  padding: 0;
  transition: color .3s linear;
}

body .ss-content .ss-list .ss-option:not(.ss-disabled).ss-selected {
  color: #010101;
  background-color: rgba(0, 0, 0, 0);
}

body .ss-content .ss-list .ss-option:is(:hover, :focus) {
  color: #010101;
  background-color: rgba(0, 0, 0, 0);
}

body .ss-content.ss-open-below {
  border-radius: 15px;
  padding: 14px 20px;
  transition: border-color .3s linear;
  transform: translateY(8px);
}

body .basicLightbox {
  background: rgba(1, 1, 1, .4);
}

.header {
  padding: 18px 0 72px;
}

.header__wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__menu {
  align-items: center;
  gap: 6px;
  display: flex;
}

.header__menu-item:not(:last-child) .header__menu-link {
  color: #e8e8e2;
  background-color: #6d8434;
  border-radius: 30px;
  padding: 10px 20px;
  font-size: 14px;
  font-style: normal;
  line-height: 1.29;
  transition: background-color .3s linear;
}

.header__menu-item:not(:last-child) .header__menu-link:is(:hover, :focus) {
  background-color: #586f1f;
}

.header__menu-link {
  align-items: center;
  display: flex;
}

.header__menu-link:is(:hover, :focus) .header__menu-space {
  background-color: #586f1f;
}

.header__menu-space {
  width: 38px;
  height: 38px;
  background-color: #6d8434;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  transition: background-color .3s linear;
  display: flex;
}

.header__menu-icon {
  width: 14px;
  height: 14px;
  fill: #e8e8e2;
}

.header__menu-cart {
  display: none;
}

.hero {
  padding-bottom: 74px;
}

.hero__wrapper {
  text-align: center;
  margin-bottom: 32px;
}

.hero__title {
  letter-spacing: -.02em;
  margin-bottom: 16px;
  font-size: 44px;
  font-weight: 500;
  line-height: 1.09;
}

.hero__title-color {
  color: #6d8434;
}

.hero__desc {
  color: rgba(1, 1, 1, .8);
  margin: 0 auto;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.hero__image {
  position: relative;
}

.hero__photo {
  border-radius: 30px;
}

.hero__label {
  width: 100px;
  height: 100px;
  position: absolute;
  top: -7px;
  right: 8px;
}

.products__wrapper {
  padding-bottom: 80px;
}

.products__filters {
  padding-bottom: 40px;
}

.products__filters-title {
  color: rgba(1, 1, 1, .3);
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.products__search {
  margin-bottom: 12px;
  position: relative;
}

.products__search-input, .products__filter-select {
  width: 100%;
  height: 48px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(1, 1, 1, .3);
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  padding: 0 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  transition: border-color .3s linear;
}

.products__search-input::-ms-input-placeholder, .products__filter-select::-ms-input-placeholder {
  color: #010101;
}

.products__search-input::placeholder, .products__filter-select::placeholder {
  color: #010101;
}

.products__search-input:is(:hover, :focus), .products__filter-select:is(:hover, :focus) {
  border-color: #6d8434;
}

.products__search-submit {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.products__search-icon {
  width: 20px;
  height: 20px;
  fill: #010101;
}

.products__filters-list {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.products__filters-item:last-child .products__filter-select {
  background-color: #fafafa;
  border-color: rgba(0, 0, 0, 0);
}

.products__filters-item:last-child .products__filter-select:is(:hover, :focus) {
  border-color: #6d8434;
}

.products__aside {
  padding-top: 80px;
}

.products__list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.products__item {
  flex-basis: 100%;
}

.products__link {
  background-color: #fafafa;
  border-radius: 20px;
  padding: 20px;
  transition: box-shadow .3s linear;
  display: block;
  position: relative;
}

.products__link:is(:hover, :focus) {
  box-shadow: 0 9.1px 16.534px rgba(0, 0, 0, .02), 0 41.6px 46.475px rgba(0, 0, 0, .04), 0 105.3px 124.678px rgba(0, 0, 0, .05), 0 208px 286px rgba(0, 0, 0, .07);
}

.products__item-space {
  background-color: #e8e8e2;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 19px 0;
}

.products__item-image {
  width: 140px;
  height: 140px;
  margin: 0 auto;
}

.products__item-info {
  margin-bottom: 40px;
}

.products__item-title {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.products__item-desc {
  flex-wrap: wrap;
  gap: 8px;
  display: flex;
}

.products__item-element {
  gap: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
  display: flex;
}

.products__item-key {
  color: rgba(1, 1, 1, .3);
}

.products__item-main {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.products__item-price {
  font-size: 18px;
  line-height: 1.11;
}

.products__item-button {
  width: 34px;
  height: 34px;
  background-color: #6d8434;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  transition: background-color .3s linear;
  display: flex;
}

.products__item-button:is(:hover, :focus) {
  background-color: #586f1f;
}

.products__item-cart {
  width: 18px;
  height: 18px;
  fill: #e8e8e2;
}

.products__item-check {
  width: 18px;
  height: 18px;
  fill: rgba(0, 0, 0, 0);
  stroke: #e8e8e2;
}

.error {
  max-width: 279px;
  text-align: center;
  margin: 0 auto;
  padding: 60px 0 100px;
  display: none;
}

.error__title {
  margin-bottom: 14px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.error__title-color {
  color: #6d8434;
}

.error__desc {
  color: rgba(1, 1, 1, .7);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.popular {
  padding-bottom: 40px;
}

.popular .products__list {
  gap: 14px;
}

.popular .products__link {
  gap: 10px;
  padding: 12px;
  display: flex;
}

.popular .products__item-space {
  width: 74px;
  height: 74px;
  margin-bottom: 0;
  padding: 9px 0;
}

.popular .products__item-image {
  width: 56px;
  height: 56px;
  margin-bottom: 0;
}

.popular .products__item-info {
  margin-bottom: 0;
}

.popular .products__item-title {
  margin-bottom: 12px;
}

.popular .products__item-desc {
  max-width: 170px;
  gap: 9px;
}

.popular .products__item-button {
  width: 20px;
  height: 20px;
  background-color: #e8e8e2;
  position: absolute;
  top: 12px;
  right: 12px;
}

.popular .products__item-cart {
  width: 12px;
  height: 12px;
  fill: #6d8434;
}

.popular .products__item-check {
  width: 12px;
  height: 12px;
  stroke: #6d8434;
}

.discount {
  padding-top: 40px;
}

.discount .products__list {
  padding-bottom: 0;
}

.discount .products__item:not(:nth-child(-n+2)) {
  display: none;
}

.discount .products__link {
  padding: 16px;
}

.discount .products__item-space {
  margin-bottom: 16px;
}

.discount .products__item-wrapper {
  align-items: center;
  gap: 8px;
  display: flex;
}

.discount .products__item-main {
  align-items: center;
}

.discount .products__item-title {
  margin-bottom: 0;
}

.discount .products__item-discount {
  width: 60px;
  height: 60px;
  position: absolute;
  top: 3px;
  right: 10px;
}

.modal {
  background-color: #fafafa;
  border-radius: 20px;
  padding: 40px 20px;
  position: relative;
}

.modal__close {
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal__close-icon {
  width: 22px;
  height: 22px;
  fill: #010101;
}

.modal .products__item-space {
  padding: 26px 0;
}

.modal .products__item-image {
  width: 160px;
  height: 160px;
}

.modal .products__item-desc {
  margin-bottom: 20px;
}

.modal .products__item-text {
  height: 92px;
  color: rgba(1, 1, 1, .7);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  overflow-y: auto;
}

.modal .products__item-button {
  width: inherit;
  height: inherit;
  color: #e8e8e2;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  font-size: 14px;
  line-height: 1.29;
  display: flex;
}

.footer {
  background-color: #6d8434;
  padding: 40px 0 20px;
}

.footer__wrapper {
  margin-bottom: 80px;
}

.footer__head {
  margin-bottom: 40px;
}

.footer .logo {
  color: #e8e8e2;
  margin-bottom: 20px;
  display: inline-flex;
}

.footer .logo__space {
  background-color: #e8e8e2;
}

.footer .logo__icon {
  fill: #6d8434;
}

.footer__social {
  align-items: center;
  gap: 12px;
  display: flex;
}

.footer__social-link {
  width: 40px;
  height: 40px;
  border: 1px solid rgba(232, 232, 226, .3);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  transition: background-color .3s linear;
  display: flex;
}

.footer__social-link:is(:hover, :focus) {
  background-color: rgba(232, 232, 226, .2);
}

.footer__social-icon {
  fill: rgba(232, 232, 226, .8);
}

.footer__info {
  margin-bottom: 80px;
}

.footer__title {
  color: #e8e8e2;
  letter-spacing: -.02em;
  margin-bottom: 14px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.footer__desc {
  color: rgba(232, 232, 226, .7);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.footer__form-text {
  color: #e8e8e2;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.footer__form-input {
  width: 100%;
  height: 42px;
  color: #e8e8e2;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #e8e8e2;
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 14px;
  padding: 0 18px;
  font-weight: 400;
  line-height: 1.5;
  display: block;
}

.footer__form-input::-ms-input-placeholder {
  color: rgba(232, 232, 226, .6);
}

.footer__form-input::placeholder {
  color: rgba(232, 232, 226, .6);
}

.footer__form-button {
  width: 100%;
  height: 42px;
  background-color: #e8e8e2;
  border-radius: 30px;
  font-size: 14px;
  line-height: 1.29;
  transition: color .3s linear;
}

.footer__form-button:is(:hover, :focus) {
  color: #6d8434;
}

.footer__foot {
  color: #e8e8e2;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.footer__foot-text {
  margin-bottom: 8px;
}

.footer__foot-list {
  align-items: center;
  gap: 8px;
  display: flex;
}

.footer__foot-item {
  gap: 8px;
  display: flex;
}

.footer__foot-item:not(:last-child):after {
  content: "/";
}

.subscription {
  width: 335px;
  background-color: #e8e8e2;
  padding: 100px 0 0;
}

.subscription__info {
  max-width: 248px;
  text-align: center;
  margin: 0 auto;
}

.subscription__title {
  margin-bottom: 14px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.subscription__title-color {
  color: #6d8434;
}

.subscription__text {
  color: rgba(1, 1, 1, .7);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.subscription__img {
  width: 100%;
  height: 144px;
  margin-top: 22px;
}

.subscription-error {
  padding: 80px 0;
}

.cart {
  padding-bottom: 80px;
}

.cart__quantity {
  align-items: center;
  gap: 14px;
  margin-bottom: 14px;
  display: flex;
}

.cart__quantity-space {
  width: 38px;
  height: 38px;
  background-color: #6d8434;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.cart__quantity-icon {
  width: 18px;
  height: 18px;
  fill: #e8e8e2;
}

.cart__quantity-counter {
  text-transform: uppercase;
