  line-height: 1.17;
}

.cart__delete {
  align-items: center;
  gap: 8px;
  margin-left: auto;
  margin-right: 7px;
  font-size: 14px;
  line-height: 1.29;
  display: flex;
}

.cart__delete-icon {
  fill: #010101;
}

.cart .products__list {
  gap: 0;
  margin-bottom: 50px;
}

.cart .products__item {
  gap: 20px;
  padding: 30px 7px 30px 0;
  display: flex;
  position: relative;
}

.cart .products__item:not(:last-child) {
  border-bottom: 1px solid rgba(1, 1, 1, .1);
}

.cart .products__delete {
  position: absolute;
  top: 18px;
  right: 7px;
}

.cart .products__item-space {
  width: 100px;
  height: 100px;
  background-color: #fafafa;
  margin-bottom: 0;
  padding: 18px 0;
}

.cart .products__item-image {
  width: 64px;
  height: 64px;
}

.cart .products__item-info {
  flex: 1;
  margin-bottom: 0;
}

.cart .products__item-desc {
  margin-bottom: 28px;
}

.cart .products__item-main {
  align-items: flex-start;
}

.cart .products__item-quantity {
  border: 1px solid rgba(1, 1, 1, .1);
  border-radius: 100px;
  align-items: center;
  gap: 6px;
  padding: 4px 12px;
  display: flex;
}

.cart .products__item-icon {
  width: 14px;
  height: 14px;
  fill: rgba(0, 0, 0, 0);
  stroke: #010101;
}

.cart .products__item-counter {
  width: 10px;
  text-align: center;
  font-weight: 400;
  line-height: 1.13;
  display: inline-block;
}

.cart__total-title {
  text-transform: uppercase;
  margin-bottom: 40px;
  font-size: 24px;
  font-weight: 500;
